<template>
  <div>
    <item class="list-item hl-item-borderless" thin>
      <div class="body-content">
          <img :src="icon" alt="" class="list-image">
          <div class="main-content">
            <div><span class="title" v-text="title"></span></div>
            <div><span class="sub-title" :class="{'fail-color':failed}">{{subTitle}}</span></div>
          </div>
          <div class="side-content">
            <button v-if="failed" @click='apeal' class="appeal-btn" type="button" name="button">申诉</button>
            <div class="num" v-else>
              <span class="yuan">元</span>
              <span class="amount">{{amount}}</span>
              <span class="plus">+</span>
            </div>
          </div>
      </div>
      <div class="clearfix"></div>
      <div v-if="failed && failReason && failReason.length">
        <div class="hairline-stand-alone shift-right"></div>
        <div class="list-item-extra-content" >
            <div class="">失败原因:</div>
            <div class="" v-for="(r, index) in failReason">{{index + 1}}.{{r}}</div>
            <div class="clearfix"></div>
        </div>
      </div>
      <div class="hairline-stand-alone shift-right"></div>
      <div class="list-item-footer" v-if="date || duration">
          <span class="left">{{date}}</span>
          <span class="right" v-if="!failed">{{duration}}</span>
          <div class="clearfix"></div>
      </div>
    </item>
  </div>
</template>
<style lang="scss" scoped>
  @import '../../scss/variables';
  @import '../../scss/mixins';

  .list-item {
    padding: 0;
    position: relative;

    .body-content,
    .list-item-extra-content {
      padding: 11px 0 13px;
      overflow: hidden;
      padding-right: 16px;
    }

    .list-item-extra-content {
      padding: 10px 0;
    }

    .fail-color {
      color: #EC4949 !important;
    }

    .appeal-btn {
      color: #EA5A49;
      border: 1px solid #EA5A49;
      border-radius: 2px;
      background-color: white;
      padding: 5px 16px;
      font-size: 12px;
      line-height: 17px;
      margin-top: 5px;
    }

    .list-item-extra-content {
      font-size: 12px;
      line-height: 22px;
      color: #888888;
    }

    .list-image {
      width: 38px;
      height: 38px;
      margin-right: 12px;
      display: block;
    }

    .list-image,
    .main-content {
      float: left;
    }

    .main-content {
      .title {
        @include font-regular();

        font-size: 14px;
        line-height: 20px;
      }

      .sub-title {
        font-size: 12px;
        line-height: 17px;
        color: #B5B5B5;
      }
    }

    .side-content {
      float: right;

      .num {
        .plus, .amount, .yuan {
          @include font-regular();

          display: block;
          float: right !important;
        }

        .plus, .yuan {
          font-size: 10px;
          line-height: 14px;
          margin-top: 13px;
        }

        .plus {
          margin-right: 1px;
        }

        .amount {
          font-size: 18px;
          line-height: 25px;
          margin-top: 6px;
        }
      }
    }

    .list-item-footer {
      padding: 12px 0 11px;
      padding-right: 16px;
      font-size: 12px;
      line-height: 17px;
      height: 40px;
      color: #B5B5B5;
    }
  }
  </style>

<script>
  export default {
    props: {
      date: String,
      duration: String,
      amount: String,
      icon: String,
      title: String,
      subTitle: String,
      taskID: String,
      failed: {
        type: Boolean,
        default: false
      },
      failReason: Object
    },
    methods: {
      apeal () {
        this.$emit('side-btn-clicked', {taskID: this.taskID, taskName: this.title, taskDate: this.date})
      }
    }
  }
</script>



