<template>
  <div class="page has-navbar" v-nav="{title: '任务进度', showBackButton: true, onBackButtonClick: back}">
    <div class="page-content gaoe-progress-page">
      <div class="list-container">
        <div class="empty-list" v-if="!(taskList && taskList.length)">
          <img src="//qianka.b0.upaiyun.com/images/bb90eb799d65bf89b626cb12026b65af.png" alt="No Data">
          暂无任务进度
        </div>
        <div  v-for="task in taskList">
          <div class="divider"></div>
          <list class="list list-ios progress-list">
            <gaoe-progress-list-item
            v-on:side-btn-clicked="apeal"
            :date="task.date"
            :amount="task.amount"
            :icon="task.icon"
            :duration="task.duration"
            :title="task.title"
            :failed= "task.status === 0 ?false : true"
            :subTitle="task.status === 0 ?'任务审核中' : '任务失败'"
            :failReason="task.failReason"
            :taskID="task.taskID"
            ></gaoe-progress-list-item>
          </list>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import '../../scss/variables';
  @import '../../scss/mixins';

  .list-container {
    .progress-list {
      padding-left: 16px;
      overflow: hidden;
      margin-bottom: 0;
    }

    .empty-list {
      position: relative;
      top: 181px;
      color: #C5C4C3;
      font-size: 16px;
      text-align: center;

      img {
        display: block;
        width: 70px;
        height: 52px;
        position: relative;
        left: 50%;
        margin-left: -35px;
        margin-bottom: 15px;
      }
    }
  }
</style>

<script>
  import GaoeProgressListItem from './GaoeProgressListItem.vue'
  import _ from 'lodash'
  import {
    KEY_GAOE_TASK_PROCESS
  } from 'constants'
  export default {
    components: {
      GaoeProgressListItem
    },
    data () {
      return {
        taskList: []
      }
    },
    mounted () {
      $api.tasksProgress()
      .then(resp => resp.data.payload)
      .then((payload) => {
        this.taskList = payload.tasks
        let process = $storage.get(KEY_GAOE_TASK_PROCESS) || []
        _.each(payload.tasks, (task) => {
          let taskProcess = String(task.id) + ':' + String(task.status)
          if (process.indexOf(taskProcess) <= -1) {
            process.push(taskProcess)
          }
        })
        $storage.set(KEY_GAOE_TASK_PROCESS, process)
      })
    },
    methods: {
      apeal (task) {
        let taskDate = this.processDate(task.taskDate)
        $router.forward({
          path: '/help/complainHitask',
          query: {backroute: '/gaoe/progress', taskId: task.taskID, taskName: task.taskName, taskDate: taskDate}
        })
      },
      back () {
        $router.back('/gaoe')
      },
      processDate (d) {
        return d.slice(0, 4) + '-' + d.slice(5, 7) + '-' + d.slice(8, 10)
      }
    }
  }
</script>



