<template>
  <div class="page" v-nav="{title: '背景介绍', showBackButton: true, onBackButtonClick: back}">
    <div class="page-content">
      <div class="gaoe-bg-content">
        <div class="gaoe-bg-md">
          <div class="md-tit">背景描述</div>
            <div class="md-content">
              <div class="company-info" v-if="data.background && data.background.establish_date">
                <span class="company-info-title">成立时间</span>
                <span class="company-info-content"> {{data.background.establish_date}} </span>
              </div>
              <div class="company-info" v-if="data.background && data.background.register_fund">
                <span class="company-info-title">注册资金</span>
                <span class="company-info-content"> {{data.background.register_fund}} </span>
              </div>
              <div class="company-info" v-if="data.background && data.background.finance_phase">
                <span class="company-info-title">融资情况</span>
                <span class="company-info-content"> {{data.background.finance_phase}} </span>
              </div>
              <div class="company-info" v-if="data.background && data.background.desc">
                <span class="company-info-title">公司简介</span>
                <span class="company-info-content"> {{data.background.desc}} </span>
              </div>
            </div>
        </div>
        <div class="gaoe-bg-md" :class="{ hide : securityState }">
          <div class="md-tit">安全保障</div>
          <div class="md-content">
            <div class="company-info" v-if="data.security && data.security.relevant_garantee">
              <span class="company-info-title">相关保障</span>
              <span class="company-info-content"> {{data.security.relevant_garantee}} </span>
            </div>
          </div>
        </div>
        <div class="web-address">官网地址：<span>{{data.website}}</span></div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  .gaoe-bg-content {
    padding-top: 44px;

    .gaoe-bg-md {
      background: #fff;

      .md-tit {
        color: #484746;
        width: 100%;
        height: 40px;
        line-height: 40px;
        font-size: 14px;
        margin-left: 16px;
        margin-top: 10px;
        border-bottom: 0.5px solid #eee;
      }

      .md-content {
        width: 100%;
        padding: 20px 16px;

        .company-info {
          color: #484746;
          width: 100%;
          height: auto;
          line-height: 22px;
          font-size: 12px;

          .company-info-title {
            display: inline-block;
            width: 20.8%;
            height: 22px;
            text-align: left;
            vertical-align: top;
          }

          .company-info-content {
            display: inline-block;
            width: 77.9%;
            height: auto;
          }
        }
      }
    }

    .web-address {
      color: #484746;
      width: 100%;
      height: 62px;
      line-height: 62px;
      margin-top: 10px;
      font-size: 13px;
      text-indent: 16px;
      background: #fff;
    }
  }
</style>
<script>
  export default {
    data () {
      return {
        data: [],
        securityState: false
      }
    },

    mounted () {
      this.loadData(this.$route.params.id)
    },

    methods: {
      back () {
        $router.back('/gaoe/detail/' + this.$route.params.id)
      },

      loadData (taskID) {
        $api.gaoeCompanyIntro(taskID)
          .then((res) => res.data && res.data.payload)
          .then((payload) => {
            this.data = payload
            payload.security.length === 0 ? this.securityState = true : this.securityState = false // 判断 security 是否为空
          })
      }
    }
  }
</script>



